import React, { FC } from 'react';
import { useForm } from 'react-hook-form';
import { Modal, ModalBody, ModalHeader } from 'reactstrap';
import { getDefaultReportViewOptions, ReportViewOptions } from '../../../../../models/dtos/ger/reportViewOptions';
import ReportForm from '../../../../components/report/ReportForm';
import { CLASS_KEY, RelacaoLancamentoSaldoRV, REPORT_EVENT, REPORT_TITLE } from './relacaoLancamentoSaldoRV';

const DEFAULT_OPTIONS: ReportViewOptions = {
  ...getDefaultReportViewOptions(REPORT_EVENT),
};

interface RelacaoLancamentoSaldoReportViewProps {
  reportValues?: RelacaoLancamentoSaldoRV;
  onClose?: () => void;
}

const RelacaoLancamentoSaldoReportView: FC<RelacaoLancamentoSaldoReportViewProps> = props => {
  const formProps = useForm<RelacaoLancamentoSaldoRV>({ defaultValues: props.reportValues, mode: 'onBlur' });

  const addExtraParameters = data => {
    const keys = Object.keys(data);
    Object.keys(props.reportValues).forEach(key => {
      if (keys.indexOf(key) === -1) {
        data[key] = props.reportValues[key];
      }
    });
    return data;
  };

  return (
    <Modal isOpen={true} centered={true} backdrop="static" toggle={props.onClose} size="lg">
      <ModalHeader toggle={props.onClose}>{REPORT_TITLE}</ModalHeader>
      <ModalBody>
        <ReportForm
          handleClose={props.onClose}
          handleSubmit={formProps.handleSubmit}
          afterSubmit={props.onClose}
          formProps={formProps}
          localStorageKey={CLASS_KEY}
          reportBasicValues={props.reportValues}
          reportViewOptions={DEFAULT_OPTIONS}
          extraConfigOpened={true}
          addExtraParameters={addExtraParameters}
        />
      </ModalBody>
    </Modal>
  );
};

export default RelacaoLancamentoSaldoReportView;
