import React, { FC, useEffect } from 'react';
import { useForm } from 'react-hook-form';
import { Col, Modal, ModalBody, ModalFooter, ModalHeader, Row } from 'reactstrap';
import { ACTION_EVENTS, alertManager, AlertType, Box, Checkbox, compare, ErroList, InputGroupInline, InputType, isEmpty, ProcessBar } from 'summer';
import { GlobalParameter } from '../../../../models/dtos/ger/globalParameter';
import { ORIGEM_LANCAMENTO_MCG_ENUM_VALUES } from '../../../../models/enumerated/mcg/origemLancamentoMCGEnum';
import { abrirContas, isDataEncerrada, removeLancamentoPeriodo } from '../../../../services/mcg.service';
import EnumUtil from '../../../../utilities/enum.util';
import { validatePeriodoConfig } from '../../../../utilities/mcg.util';

import './LancamentoExclusaoPeriodoModalView.css';

interface LancamentoExclusaoPeriodoModalViewProps {
  onClose: (update: boolean) => void;
  globalParameter: GlobalParameter;
  expressions: any[];
}

interface ScreenFields {
  dtInicial?: string;
  dtFinal?: string;
  todas?: boolean;
  conciliacaoBancaria?: boolean;
  contabilidade?: boolean;
  controlePatrimonial?: boolean;
  folhaPagamento?: boolean;
  folhaPagamento131?: boolean;
  folhaPagamento132?: boolean;
  folhaPagamentoAdiantamento?: boolean;
  folhaPagamentoDarf?: boolean;
  folhaPagamentoFerias?: boolean;
  folhaPagamentoGps132?: boolean;
  folhaPagamentoGpsCooperativa?: boolean;
  folhaPagamentoGpsGeral?: boolean;
  folhaPagamentoGpsProdutor?: boolean;
  folhaPagamentoGpsTerceiros?: boolean;
  folhaPagamentoRescisao?: boolean;
  folhaPagamentoResilicao?: boolean;
  honorarios?: boolean;
  importacao?: boolean;
  integracao?: boolean;
  livrosFiscais?: boolean;
  sinf?: boolean;
}

const checkBoxEnumMap = {
  conciliacaoBancaria: 'CONCILIACAO',
  contabilidade: 'CONTABILIDADE',
  controlePatrimonial: 'CONTROLE_PATRIMONIAL',
  folhaPagamento: 'FOLHA_DE_PAGAMENTO_FOLHA',
  folhaPagamento131: 'FOLHA_DE_PAGAMENTO_13_PARCELA_1',
  folhaPagamento132: 'FOLHA_DE_PAGAMENTO_13_PARCELA_2',
  folhaPagamentoAdiantamento: 'FOLHA_DE_PAGAMENTO_ADIANTAMENTO',
  folhaPagamentoDarf: 'FOLHA_DE_PAGAMENTO_DARF',
  folhaPagamentoFerias: 'FOLHA_DE_PAGAMENTO_FERIAS',
  folhaPagamentoGps132: 'FOLHA_DE_PAGAMENTO_GPS_13_PARCELA_2',
  folhaPagamentoGpsCooperativa: 'FOLHA_DE_PAGAMENTO_GPS_COOPERATIVA',
  folhaPagamentoGpsGeral: 'FOLHA_DE_PAGAMENTO_GPS_GERAL',
  folhaPagamentoGpsProdutor: 'FOLHA_DE_PAGAMENTO_GPS_PRODUTOR_RURAL',
  folhaPagamentoGpsTerceiros: 'FOLHA_DE_PAGAMENTO_GPS_TERCEIROS_TRANSPORTADOR',
  folhaPagamentoRescisao: 'FOLHA_DE_PAGAMENTO_RESCISAO',
  folhaPagamentoResilicao: 'FOLHA_DE_PAGAMENTO_RESILICAO',
  honorarios: 'HONORARIOS',
  importacao: 'IMPORTACAO',
  integracao: 'INTEGRACAOREST',
  livrosFiscais: 'LIVRO_FISCAL',
  sinf: 'SINF',
};

const LancamentoExclusaoPeriodoModalView: FC<LancamentoExclusaoPeriodoModalViewProps> = props => {
  const formProps = useForm<ScreenFields>({ defaultValues: { contabilidade: true }, mode: 'onBlur' });

  useEffect(
    () => {
      setTimeout(() => {
        const values = props.expressions[0].expressions
          .map(it => ({ [it.alias || it.fieldName]: it.value }))
          .reduce((result, it) => ({ ...result, ...it }));

        formProps.setValue([{ dtInicial: values.dtLancamentoInicio }, { dtFinal: values.dtLancamentoFinal }]);
      }, 700);
    },
    // eslint-disable-next-line react-hooks/exhaustive-deps
    []
  );

  const onClose = () => {
    if (props.onClose) {
      props.onClose(false);
    }
  };

  const toggleTodas = () => {
    const values = formProps.getValues();
    formProps.setValue(Object.keys(checkBoxEnumMap).map(key => ({ [key]: values.todas })));
  };

  const validateExtras = formData => {
    const erros = [];

    if (isEmpty(formData.dtInicial)) {
      erros.push('Dt. Inicial: Campo no pode estar em branco');
    }

    if (isEmpty(formData.dtFinal)) {
      erros.push('Dt. Final: Campo no pode estar em branco');
    }

    if (compare(formData.dtInicial, formData.dtFinal) > 0) {
      erros.push('Dt. Inicial no pode ser posterior  Dt. Final.');
    }

    const keysCheckboxes = Object.keys(checkBoxEnumMap);

    if (Object.keys(formData).filter(key => keysCheckboxes.indexOf(key) !== -1).length === 0) {
      erros.push('Pelo menos uma origem deve ser selecionada.');
    }

    return erros;
  };

  const execRemoverLancamentos = formData => {
    const keys = Object.keys(checkBoxEnumMap);
    const origens = Object.keys(formData)
      .filter(key => keys.indexOf(key) !== -1 && formData[key] === true)
      .map(key => EnumUtil.find(ORIGEM_LANCAMENTO_MCG_ENUM_VALUES, checkBoxEnumMap[key]))
      .map(item => item.extraFields.intValue);

    removeLancamentoPeriodo(
      {
        dtFinalStr: formData.dtFinal,
        dtInicialStr: formData.dtInicial,
        idGerEmpresa: props.globalParameter.empresa.id,
        origens,
      },
      {
        thenFunction: res => {
          if (res) {
            alertManager.emit(ACTION_EVENTS.SHOW, {
              message: res,
              type: AlertType.ERROR,
            });
          } else {
            props.onClose(true);
          }
        },
      }
    );
  };

  const removerLancamentos = formData => {
    const params = {
      data: formData.dtInicial,
      idGerEmpresa: props.globalParameter.empresa.id,
    };

    isDataEncerrada(params, {
      thenFunction: isEncerrado => {
        if (isEncerrado) {
          alertManager.emit(ACTION_EVENTS.SHOW, {
            message:
              'Existem contas de resultado encerradas neste perodo. Continuar com essa operao ocasionar ' +
              'a abertura das mesmas. Deseja continuar?',
            onOkClick: () => {
              abrirContas(params, {
                thenFunction: () => {
                  execRemoverLancamentos(formData);
                },
              });
            },
            type: AlertType.WARNING_YES_NO,
          });
        } else {
          execRemoverLancamentos(formData);
        }
      },
    });
  };

  const onSubmit = () => {
    const values = formProps.getValues();
    const erros = validateExtras(values);

    if (erros.length > 0) {
      alertManager.emit(ACTION_EVENTS.SHOW, {
        message: <ErroList erros={erros} />,
        type: AlertType.ERROR,
      });
    } else {
      const bloqueio = validatePeriodoConfig(values.dtInicial, props.globalParameter) || validatePeriodoConfig(values.dtFinal, props.globalParameter);
      if (bloqueio) {
        alertManager.emit(ACTION_EVENTS.SHOW, {
          message: 'No  possvel excluir lanamentos ' + bloqueio,
          type: AlertType.ERROR,
        });
      } else {
        alertManager.emit(ACTION_EVENTS.SHOW, {
          message: `Confirma a excluso dos lanamentos no perodo de ${values.dtInicial}  ${values.dtFinal}?`,
          onOkClick: () => removerLancamentos(values),
          type: AlertType.WARNING_YES_NO,
        });
      }
    }
  };

  return (
    <Modal isOpen={true} centered={true} backdrop="static" toggle={onClose} size="xl">
      <ModalHeader toggle={onClose}>Lanamento - Excluso por Perodo</ModalHeader>
      <ModalBody>
        <Row>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { required: true } }}
              isPrependLabel={true}
              id="dtInicial"
              label="Dt. Inicial"
              type={InputType.DATE}
            />
          </Col>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { required: true } }}
              isPrependLabel={true}
              id="dtFinal"
              label="Dt. Final"
              type={InputType.DATE}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <Box title="Origem" className="checkbox-box">
              <Row>
                <Col>
                  <Checkbox
                    formProps={formProps}
                    id="todas"
                    label="Todos"
                    onChange={toggleTodas}
                    labelProps={{ style: { fontWeight: 'bold', marginBottom: 0 } }}
                  />
                </Col>
                <Col>
                  <Checkbox formProps={formProps} id="contabilidade" label="Contabilidade" />
                </Col>
              </Row>
              <Row>
                <Col>
                  <Checkbox formProps={formProps} id="conciliacaoBancaria" label="Conciliao Bancria" />
                </Col>
                <Col>
                  <Checkbox formProps={formProps} id="controlePatrimonial" label="Controle de Patrimnial" />
                </Col>
              </Row>
              <Row>
                <Col>
                  <Checkbox formProps={formProps} id="folhaPagamento" label="Folha de Pagamento" />
                </Col>
                <Col>
                  <Checkbox formProps={formProps} id="folhaPagamento131" label="Folha de Pagamento - 13 - 1 Parcela" />
                </Col>
              </Row>
              <Row>
                <Col>
                  <Checkbox formProps={formProps} id="folhaPagamento132" label="Folha de Pagamento - 13 - 1 Parcela" />
                </Col>
                <Col>
                  <Checkbox formProps={formProps} id="folhaPagamentoAdiantamento" label="Folha de Pagamento - Adiantamento" />
                </Col>
              </Row>
              <Row>
                <Col>
                  <Checkbox formProps={formProps} id="folhaPagamentoFerias" label="Folha de Pagamento - Frias" />
                </Col>
                <Col>
                  <Checkbox formProps={formProps} id="folhaPagamentoRescisao" label="Folha de Pagamento - Resciso" />
                </Col>
              </Row>
              <Row>
                <Col>
                  <Checkbox formProps={formProps} id="folhaPagamentoResilicao" label="Folha de Pagamento - Resciso" />
                </Col>
                <Col>
                  <Checkbox formProps={formProps} id="folhaPagamentoGpsGeral" label="Folha de Pagamento - Gps - Geral" />
                </Col>
              </Row>
              <Row>
                <Col>
                  <Checkbox formProps={formProps} id="folhaPagamentoGps132" label="Folha de Pagamento - Gps -  13 - 2 Parcela" />
                </Col>
                <Col>
                  <Checkbox formProps={formProps} id="folhaPagamentoGpsCooperativa" label="Folha de Pagamento - Gps - Cooperativa" />
                </Col>
              </Row>
              <Row>
                <Col>
                  <Checkbox formProps={formProps} id="folhaPagamentoGpsProdutor" label="Folha de Pagamento - Gps -  Produtor Rural" />
                </Col>
                <Col>
                  <Checkbox formProps={formProps} id="folhaPagamentoGpsTerceiros" label="Folha de Pagamento - Gps - Terceiros Transportador" />
                </Col>
              </Row>
              <Row>
                <Col>
                  <Checkbox formProps={formProps} id="folhaPagamentoDarf" label="Folha de Pagamento - DARF" />
                </Col>
                <Col>
                  <Checkbox formProps={formProps} id="honorarios" label="Honorrios" />
                </Col>
              </Row>
              <Row>
                <Col>
                  <Checkbox formProps={formProps} id="importacao" label="Importao" />
                </Col>
                <Col>
                  <Checkbox formProps={formProps} id="integracao" label="Integrao (JSON)" />
                </Col>
              </Row>
              <Row>
                <Col>
                  <Checkbox formProps={formProps} id="livrosFiscais" label="Livros Fiscais" />
                </Col>
                <Col>
                  <Checkbox formProps={formProps} id="sinf" label="SINF" />
                </Col>
              </Row>
            </Box>
          </Col>
        </Row>
      </ModalBody>
      <ModalFooter>
        <ProcessBar handleSubmit={formProps.handleSubmit(onSubmit)} handleClose={onClose} />
      </ModalFooter>
    </Modal>
  );
};

export default LancamentoExclusaoPeriodoModalView;
