export interface BrowserPrefsProps {
  set: (key: string, value: any) => boolean;
  get: (key: string) => any;
  has: (key: string) => boolean;
  del: (key: string) => boolean;
}

export const BROWSER_PREFERENCES_KEY_NAME = 'userPreferences';

const useBrowserPreferences = (): BrowserPrefsProps => {
  const settedPrefs = window.localStorage.getItem(BROWSER_PREFERENCES_KEY_NAME);
  const prefs = settedPrefs ? JSON.parse(settedPrefs) : {};

  const writePrefs = () => {
    window.localStorage.setItem(BROWSER_PREFERENCES_KEY_NAME, JSON.stringify(prefs));
  };

  const checkKey = key => typeof key === 'string' && key.search(/\s/g) === -1;

  const get = (key: string): any => checkKey(key) && prefs[key];
  const has = (key: string): boolean => checkKey(key) && Object.keys(prefs).indexOf(key) !== -1;

  const set = (key: string, value: any): boolean => {
    const result = checkKey(key);
    if (result) {
      prefs[key] = value;
      writePrefs();
    }
    return result;
  };

  const del = (key: string): boolean => {
    const result = checkKey(key) && has(key);
    if (result) {
      delete prefs[key];
      writePrefs();
    }
    return result;
  };

  return { set, get, has, del };
};

export default useBrowserPreferences;
