import Pagination from 'rc-pagination';
import localeInfo from 'rc-pagination/lib/locale/pt_BR';
import React, { forwardRef, useEffect, useImperativeHandle, useRef, useState } from 'react';
import BootstrapTable from 'react-bootstrap-table-next';
import filterFactory from 'react-bootstrap-table2-filter';
import ToolkitProvider from 'react-bootstrap-table2-toolkit';
import { useForm } from 'react-hook-form';
import { useSelector } from 'react-redux';
import { Card, Col, FormGroup, Input, Label, Row } from 'reactstrap';
import { ColumnsProps, decimalFormatter, formatDate, formatSaldo, SearchExpression, SelectModeEnum } from 'summer';
import { getDefaultReportBasicValues } from '../../../../models/dtos/ger/reportBasicValues';
import { SaldoCriteria } from '../../../../models/dtos/mcg/saldoCriteria';
import { CODIGO_REDUZIDO as CODIGO_REDUZIDO_PLANO } from '../../../../models/enumerated/ger/planoContaGERTipoLancamentoContaEnum';
import { consultarMovimentacao, getSaldoAnterior } from '../../../../services/mcg.service';
import { Reducers } from '../../../../store/ducks';
import { GlobalState } from '../../../../store/ducks/global.duck';
import RelacaoLancamentoSaldoReportView from '../report/relacaoLancamentoSaldo/RelacaoLancamentoSaldoReportView';
import { RelacaoLancamentoSaldoRV, REPORT_TITLE } from '../report/relacaoLancamentoSaldo/relacaoLancamentoSaldoRV';

import 'react-bootstrap-table-next/dist/react-bootstrap-table2.min.css';
import 'react-bootstrap-table2-toolkit/dist/react-bootstrap-table2-toolkit.min.css';
import './LancamentoMovimentacaoDataGrid.css';

interface LancamentoMovimentacaoDataGridProps {
  expressions: () => SearchExpression[];
  columns: ColumnsProps[];
}

export interface LancamentoMovimentacaoDataGridRefProps {
  doSearch?: () => void;
  imprimir?: (descConta?: string, descCentroCusto?: string) => void;
}

interface ScreenFields {
  saldoAnterior?: string;
  totalDebito?: string;
  totalCredito?: string;
  saldoAtual?: string;
}

const LancamentoMovimentacaoDataGrid = forwardRef<LancamentoMovimentacaoDataGridRefProps, LancamentoMovimentacaoDataGridProps>((props, ref) => {
  const globalReducer = useSelector<Reducers, GlobalState>(state => state.globalReducer);
  const formProps = useForm<ScreenFields>({ mode: 'onBlur' });
  const tableRef = useRef<HTMLDivElement>();

  const [dados, setDados] = useState([]);
  const [dadosPaginados, setDadosPaginados] = useState([]);
  const [pagination, setPagination] = useState({ page: 1, sizePerPage: 10, totalSize: 0 });
  const [columns, setColumns] = useState(props.columns);
  const [currentCriteria, setCurrentCriteria] = useState<SaldoCriteria>(null);
  const [modalContent, setModalContent] = useState(null);

  useEffect(
    () => {
      setColumns(c => {
        c.splice(8, 0, {
          dataField: 'saldo',
          formatter: formatSaldo,
          sort: true,
          text: 'Saldo',
        });
        return [...c];
      });
    },
    // eslint-disable-next-line react-hooks/exhaustive-deps
    []
  );

  const closeModal = () => setModalContent(null);

  const afterSearch = (data, criteria) => {
    let saldoAtual = 0;
    let totalCredito = 0;
    let totalDebito = 0;

    if (data.length > 0) {
      saldoAtual = data[data.length - 1].saldo;
      data.forEach(item => {
        if (criteria.conta === item.contaCredito) {
          totalCredito += item.valor;
        }
        if (criteria.conta === item.contaDebito) {
          totalDebito += item.valor;
        }
      });
    }

    formProps.setValue([
      { saldoAtual: formatSaldo(saldoAtual) },
      { totalCredito: decimalFormatter(totalCredito, null) },
      { totalDebito: decimalFormatter(totalDebito, null) },
    ]);

    getSaldoAnterior(criteria, {
      thenFunction: res => {
        formProps.setValue('saldoAnterior', formatSaldo(res));
        if (saldoAtual === 0) {
          formProps.setValue('saldoAtual', formatSaldo(res));
        }
      },
    });
  };

  const doSearch = () => {
    const values = props
      .expressions()[0]
      .expressions.map(it => ({ [it.alias || it.fieldName]: it.value }))
      .reduce((result, it) => ({ ...result, ...it }));
    const criteria: SaldoCriteria = {
      consolidar: values.consolidar,
      conta: values.conta,
      dtAte: formatDate(values.dtLancamentoFinal),
      dtDe: formatDate(values.dtLancamentoInicio),
      idGerEmpresa: globalReducer.globalParameter.empresa.id,
      idGerGrupoEmpresaAtual: globalReducer.globalParameter.empresa.grupoEmpresa.id,
      idGerPlanoConta: values.idGerPlanoConta,
    };

    if (values.centroCusto) {
      criteria.idGerCentroCusto = values.idGerCentroCusto;
      criteria.centroCusto = values.centroCusto;
    }

    setCurrentCriteria(criteria);
    consultarMovimentacao(criteria, {
      thenFunction: res => {
        afterSearch(res, criteria);
        setDados(res);
        setPagination(p => ({ ...p, totalSize: res.length }));
      },
    });
  };

  const imprimir = (descConta = null, descCentroCusto = null) => {
    const reportValues: RelacaoLancamentoSaldoRV = {
      ...getDefaultReportBasicValues(REPORT_TITLE),
      codigoReduzido: globalReducer.globalParameter.empresaMCG.tipoLancamentoConta === CODIGO_REDUZIDO_PLANO.value,
      consolidar: currentCriteria.consolidar,
      descConta,
      dtFinal: currentCriteria.dtAte,
      dtInicial: currentCriteria.dtDe,
      idConta: currentCriteria.idGerPlanoConta,
      idGerEmpresa: globalReducer.globalParameter.empresa.id.toString(),
      idGerGrupoEmpresa: globalReducer.globalParameter.empresa.grupoEmpresa.id,
      numConta: currentCriteria.conta,
      saldos: dados,
    };

    if (currentCriteria.centroCusto) {
      reportValues.idCentroCusto = currentCriteria.idGerCentroCusto;
      reportValues.numCentroCusto = currentCriteria.centroCusto;
      reportValues.descCentroCusto = descCentroCusto;
    }

    setModalContent(<RelacaoLancamentoSaldoReportView reportValues={reportValues} onClose={closeModal} />);
  };

  useImperativeHandle(ref, () => ({
    doSearch,
    imprimir,
  }));

  useEffect(
    () => {
      const begin = pagination.sizePerPage * (pagination.page - 1);
      setDadosPaginados(Array.from(Array(pagination.sizePerPage), (_, i) => dados[begin + i]).filter(it => it !== undefined));
    },
    // eslint-disable-next-line react-hooks/exhaustive-deps
    [pagination]
  );

  const onPageChange = page => {
    setPagination(p => ({ ...p, page }));
    tableRef.current.scroll(0, 0);
  };

  const selectRow = {
    clickToSelect: true,
    mode: SelectModeEnum.RADIO,
  };

  return (
    <>
      {modalContent}
      <ToolkitProvider keyField="id" columns={columns} data={dadosPaginados} bootstrap4={true} search={true}>
        {toolkitProps => [
          <div key="TableMovimentacao" className="table-area table-movimentacao" ref={tableRef}>
            <BootstrapTable
              {...toolkitProps.baseProps}
              striped={true}
              bordered={true}
              hover={true}
              responsive={true}
              tabIndexCell={true}
              filter={filterFactory()}
              selectRow={selectRow}
            />
          </div>,
          <div key="pgMovimentacao" className="p-3 rounded bg-secondary d-flex justify-content-center align-items-center mt-3 position-relative">
            <div style={{ position: 'absolute', left: '1rem' }}>Registros: {pagination.totalSize}</div>
            <Pagination
              showQuickJumper={true}
              locale={localeInfo}
              pageSize={pagination.sizePerPage}
              current={pagination.page}
              total={pagination.totalSize}
              onChange={onPageChange}
            />
          </div>,
          <Card className="custom-footer mt-4" body={true} key="footerMovimentacao">
            <Row>
              <Col>
                <FormGroup>
                  <Label>Saldo Anterior:</Label>
                  <Input name="saldoAnterior" innerRef={formProps.register} disabled={true} bsSize="sm" />
                </FormGroup>
              </Col>
              <Col>
                <FormGroup>
                  <Label>Total de Dbito:</Label>
                  <Input name="totalDebito" innerRef={formProps.register} disabled={true} bsSize="sm" />
                </FormGroup>
              </Col>
              <Col>
                <FormGroup>
                  <Label>Total de Crdito:</Label>
                  <Input name="totalCredito" innerRef={formProps.register} disabled={true} bsSize="sm" />
                </FormGroup>
              </Col>
              <Col>
                <FormGroup>
                  <Label>Saldo Atual:</Label>
                  <Input name="saldoAtual" innerRef={formProps.register} disabled={true} bsSize="sm" />
                </FormGroup>
              </Col>
            </Row>
          </Card>,
        ]}
      </ToolkitProvider>
    </>
  );
});

export default LancamentoMovimentacaoDataGrid;
