import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import cx from 'clsx';
import React, { FC, useEffect, useRef, useState } from 'react';
import { useForm } from 'react-hook-form';
import { useSelector } from 'react-redux';
import { Button, Card, Col, Collapse, FormGroup, Input, Label, Row } from 'reactstrap';
import {
  Accordion,
  AccordionItem,
  ACTION_EVENTS,
  alertManager,
  AlertType,
  ColumnsProps,
  decimalFormatter,
  DefaultSortProps,
  formatDate,
  formatRef,
  getExtraData,
  InputGroupInline,
  InputType,
  isEmpty,
  OperationEnum,
  OrderType,
  parseLastDay,
  parseRefToDate,
  SearchDataGrid,
  SearchDataGridRefProps,
  SearchExpression,
  SearchViewProps,
  SelectModeEnum,
  toDate,
  WindowComponent,
} from 'summer';
import { hasEmpresa } from '../../../../dependencies/empresaDependency';
import { hasEmpresaMCG } from '../../../../dependencies/empresaMCGDependency';
import { hasGrupoPlanoContas } from '../../../../dependencies/grupoPlanoContasDependency';
import useBrowserPreferences from '../../../../hooks/useBrowserPreferences';
import { CentroCusto } from '../../../../models/dtos/ger/centroCusto';
import { PlanoConta } from '../../../../models/dtos/ger/planoConta';
import { ConsultaTotalLancamento } from '../../../../models/dtos/mcg/consultaTotalLancamento';
import { CLASS_NAME, LancamentoSaldo } from '../../../../models/dtos/mcg/lancamentoSaldo';
import { CODIGO_REDUZIDO as CODIGO_REDUZIDO_CENTRO } from '../../../../models/enumerated/ger/centroCustoGERTipoLancamentoCentroCustoEnum';
import { CODIGO_REDUZIDO as CODIGO_REDUZIDO_PLANO } from '../../../../models/enumerated/ger/planoContaGERTipoLancamentoContaEnum';
import { CONSOLIDAR_ENUM_VALUES, NORMAL } from '../../../../models/enumerated/mcg/consolidarEnum';
import { ORIGEM_LANCAMENTO_MCG_ENUM_VALUES } from '../../../../models/enumerated/mcg/origemLancamentoMCGEnum';
import {
  abrirContas,
  atualizaCalculoIrpjCsll,
  isDataEncerrada,
  removerLancamentoLote,
  totalizadorLancamento,
} from '../../../../services/mcg.service';
import { Reducers } from '../../../../store/ducks';
import { GlobalState } from '../../../../store/ducks/global.duck';
import EnumUtil from '../../../../utilities/enum.util';
import { validatePeriodoConfig } from '../../../../utilities/mcg.util';
import CentroCustoIS from '../../../components/mcg/centroCustoIS/CentroCustoIS';
import PlanoContaIS from '../../../components/mcg/planoContaIS/PlanoContaIS';
import LancamentoExclusaoPeriodoModalView from './LancamentoExclusaoPeriodoModalView';
import LancamentoMovimentacaoDataGrid, { LancamentoMovimentacaoDataGridRefProps } from './LancamentoMovimentacaoDataGrid';

import './LancamentoSaldoSearchView.css';

interface LancamentoSaldoSearchViewProps extends SearchViewProps<LancamentoSaldo> {}

interface SearchFields {
  referencia?: string;
  dataInicial?: string;
  dataFinal?: string;
  contaDebito?: PlanoConta;
  contaCredito?: PlanoConta;
  origemLancamento?: string;
  planoContaSaldo?: PlanoConta;
  consolidacaoSaldo?: string;
  centroCustoSaldo?: CentroCusto;
  descDebito?: string;
  descCredito?: string;
  descCustoDebito?: string;
  descCustoCredito?: string;
  totalSaldo?: string;
  totalGridDebito?: string;
  totalGridCredito?: string;
}

interface OpenFromVerifyError {
  isFromVerifyError: boolean;
  planoConta: PlanoConta;
  dtLancamento: string;
}

const defaultSorted: DefaultSortProps[] = [
  {
    dataField: 'data',
    order: OrderType.ASC,
  },
];

const BROWSER_BUSCA_KEY = 'lancamentoBuscaExpandida';

const LancamentoSaldoSearchView: FC<LancamentoSaldoSearchViewProps & OpenFromVerifyError> = props => {
  const globalReducer = useSelector<Reducers, GlobalState>(state => state.globalReducer);
  const browserPrefs = useBrowserPreferences();

  const formProps = useForm<SearchFields>({
    defaultValues: {
      referencia: globalReducer.globalParameter.empresaMCG.refAtual,
    },
    mode: 'onBlur',
  });

  const searchRef = useRef<SearchDataGridRefProps>();
  const movimentacaoRef = useRef<LancamentoMovimentacaoDataGridRefProps>();

  const [isSearchOpen, setIsSearchOpen] = useState(browserPrefs.get(BROWSER_BUSCA_KEY) || false);
  const [modoBusca, setModoBusca] = useState('lancamentos');
  const [modalContent, setModalContent] = useState(null);
  const [openFromMenu, setOpenFromMenu] = useState(true);
  const [openFromVerifyError, setOpenFromVerifyError] = useState(false);

  const defaultSearchExpression: SearchExpression = {
    expressions: [],
    fieldName: 'idEmpresa',
    value: globalReducer.globalParameter.empresa.id,
  };

  useEffect(
    () => {
      if (props.isFromVerifyError) {
        setOpenFromMenu(false);
        setOpenFromVerifyError(true);
        setTimeout(() => {
          formProps.setValue('referencia', null);
          formProps.setValue('dataInicial', props.dtLancamento);
          formProps.setValue('dataFinal', props.dtLancamento);
          formProps.setValue('planoContaSaldo', props.planoConta);
          formProps.setValue('consolidacaoSaldo', NORMAL.value, true);
        }, 800);
      } else {
        setTimeout(() => {
          if (searchRef.current) {
            searchRef.current.notifySearchExpressionChange();
          }
        }, 800);
      }
    },
    // eslint-disable-next-line react-hooks/exhaustive-deps
    []
  );

  const buildRefExpression = ref => {
    return [
      {
        alias: 'dtLancamentoInicio',
        fieldName: 'dtLancamento',
        operation: OperationEnum.GREATER_THAN_EQUAL,
        value: parseRefToDate(ref, 1),
      },
      {
        alias: 'dtLancamentoFinal',
        fieldName: 'dtLancamento',
        operation: OperationEnum.LESS_THAN_EQUAL,
        value: parseLastDay(ref),
      },
    ];
  };

  const toggleSearch = () =>
    setIsSearchOpen(state => {
      browserPrefs.set(BROWSER_BUSCA_KEY, !state);
      return !state;
    });

  const contaDebitoOuCRFormatter = (cell, row) => {
    let result = '';
    if (globalReducer.globalParameter.empresaMCG.tipoLancamentoConta === CODIGO_REDUZIDO_PLANO.value) {
      if (row.codigoReduzidoDebito) {
        result = row.codigoReduzidoDebito;
      }
    } else {
      if (row.contaDebito) {
        result = row.contaDebito;
      }
    }
    return result;
  };

  const contaCreditoOuCRFormatter = (cell, row) => {
    let result = '';
    if (globalReducer.globalParameter.empresaMCG.tipoLancamentoConta === CODIGO_REDUZIDO_PLANO.value) {
      if (row.codigoReduzidoCredito) {
        result = row.codigoReduzidoCredito;
      }
    } else {
      if (row.contaCredito) {
        result = row.contaCredito;
      }
    }
    return result;
  };

  const centroCustoCreditoOuCRFormatter = (cell, row) => {
    let result = '';
    if (globalReducer.globalParameter.empresaMCG.tipoLancamentoCentroCusto === CODIGO_REDUZIDO_CENTRO.value) {
      if (row.codigoReduzidoCentroCustoCredito) {
        result = row.codigoReduzidoCentroCustoCredito;
      }
    } else {
      if (row.codigoCentroCustoCredito) {
        result = row.codigoCentroCustoCredito;
      }
    }
    return result;
  };

  const centroCustoDebitoOuCRFormatter = (cell, row) => {
    let result = '';
    if (globalReducer.globalParameter.empresaMCG.tipoLancamentoCentroCusto === CODIGO_REDUZIDO_CENTRO.value) {
      if (row.codigoReduzidoCentroCustoDebito) {
        result = row.codigoReduzidoCentroCustoDebito;
      }
    } else {
      if (row.codigoCentroCustoDebito) {
        result = row.codigoCentroCustoDebito;
      }
    }
    return result;
  };

  const origemFormatter = cell => EnumUtil.find(ORIGEM_LANCAMENTO_MCG_ENUM_VALUES, cell, 'intValue').label;

  const zerarDatas = () => formProps.setValue([{ dataInicial: null }, { dataFinal: null }]);
  const zerarReferencia = () => formProps.setValue('referencia', null);
  const zerarContaSaldo = () => formProps.setValue([{ planoContaSaldo: null }, { centroCustoSaldo: null }]);
  const zerarConta = () => formProps.setValue([{ contaDebito: null }, { contaCredito: null }, { origemLancamento: null }]);

  const changeContaSaldo = value => {
    zerarConta();
    if (value && !value.analitica) {
      alertManager.emit(ACTION_EVENTS.SHOW, {
        message: 'Conta de Saldo - Conta deve ser analtica',
        type: AlertType.INFORMATION,
      });
      formProps.setValue('planoContaSaldo', null);
    }
  };

  const getSearchExpressions = () => {
    const expressions = [];

    const values = formProps.getValues();

    if (values.referencia) {
      expressions.push(...buildRefExpression(values.referencia));
    } else {
      if (values.dataInicial) {
        expressions.push({
          alias: 'dtLancamentoInicio',
          fieldName: 'dtLancamento',
          operation: OperationEnum.GREATER_THAN_EQUAL,
          value: toDate(values.dataInicial),
        });
      }
      if (values.dataFinal) {
        expressions.push({
          alias: 'dtLancamentoFinal',
          fieldName: 'dtLancamento',
          operation: OperationEnum.LESS_THAN_EQUAL,
          value: toDate(values.dataFinal),
        });
      }
    }

    if (values.origemLancamento) {
      expressions.push({
        fieldName: 'origem',
        operation: OperationEnum.EQUAL,
        value: values.origemLancamento,
      });
    }

    if (values.consolidacaoSaldo) {
      expressions.push({
        fieldName: 'consolidar',
        value: values.consolidacaoSaldo,
      });

      if (values.planoContaSaldo) {
        expressions.push(
          {
            fieldName: 'conta',
            value: values.planoContaSaldo.conta,
          },
          {
            fieldName: 'idGerPlanoConta',
            value: values.planoContaSaldo.id,
          }
        );
      }

      if (values.centroCustoSaldo) {
        expressions.push(
          {
            fieldName: 'centroCusto',
            value: values.centroCustoSaldo.codigo,
          },
          {
            fieldName: 'idGerCentroCusto',
            value: values.centroCustoSaldo.id,
          }
        );
      }
    } else {
      if (values.contaDebito) {
        expressions.push({
          fieldName: 'contaDebito',
          operation: OperationEnum.CONTAINS,
          value: values.contaDebito.id,
        });
      }

      if (values.contaCredito) {
        expressions.push({
          fieldName: 'contaCredito',
          operation: OperationEnum.CONTAINS,
          value: values.contaCredito.id,
        });
      }
    }

    return [{ ...defaultSearchExpression, expressions }];
  };

  const doSearch = () => {
    const values = formProps.getValues();

    let result = true;

    if (values.planoContaSaldo || values.consolidacaoSaldo) {
      if (isEmpty(values.planoContaSaldo)) {
        result = false;
        alertManager.emit(ACTION_EVENTS.SHOW, {
          message: 'Conta - Campo no pode estar vazio',
          type: AlertType.INFORMATION,
        });
      }

      if (result && isEmpty(values.consolidacaoSaldo)) {
        result = false;
        alertManager.emit(ACTION_EVENTS.SHOW, {
          message: 'Consolidao - Campo no pode estar vazio',
          type: AlertType.INFORMATION,
        });
      }
    } else {
      if (isEmpty(values.referencia) && isEmpty(values.dataInicial) && isEmpty(values.dataFinal)) {
        result = false;
        alertManager.emit(ACTION_EVENTS.SHOW, {
          message: 'Referncia, Data Inicial e Data Final - Um dos campos deve ser preeenchido',
          type: AlertType.INFORMATION,
        });
      }
    }

    if (result) {
      if (values.planoContaSaldo || values.consolidacaoSaldo) {
        setModoBusca('movimentacao');
        movimentacaoRef.current.doSearch();
      } else {
        setModoBusca('lancamentos');
        searchRef.current.notifySearchExpressionChange();
      }
    }
  };

  const afterSearch = () => {
    const values = formProps.getValues();

    const dtDeStr = values.referencia ? formatDate(parseRefToDate(values.referencia, 1)) : values.dataInicial;
    const dtAteStr = values.referencia ? formatDate(parseLastDay(values.referencia)) : values.dataFinal;

    const params: ConsultaTotalLancamento = {
      dtAteStr,
      dtDeStr,
      idGerEmpresa: globalReducer.globalParameter.empresa.id,
    };

    if (values.contaDebito) {
      params.idPlanoContaDebito = values.contaDebito.id;
    }

    if (values.contaCredito) {
      params.idPlanoContaCredito = values.contaCredito.id;
    }

    if (values.origemLancamento) {
      params.origemLancamento = values.origemLancamento;
    }

    totalizadorLancamento(params, {
      thenFunction: res =>
        formProps.setValue([{ totalSaldo: res.saldoTotal }, { totalGridDebito: res.totalDebito }, { totalGridCredito: res.totalCredito }]),
    });
  };

  const onSelect = row => {
    const descCredito = contaCreditoOuCRFormatter(null, row);
    const descDebito = contaDebitoOuCRFormatter(null, row);
    const descCustoCredito = centroCustoCreditoOuCRFormatter(null, row);
    const descCustoDebito = centroCustoDebitoOuCRFormatter(null, row);

    formProps.setValue([
      { descCredito: descCredito ? descCredito + ' - ' + row.descricaoCredito : null },
      { descDebito: descDebito ? descDebito + ' - ' + row.descricaoDebito : null },
      { descCustoCredito: descCustoCredito ? descCustoCredito + ' - ' + row.descricaoCentroCustoCredito : null },
      { descCustoDebito: descCustoDebito ? descCustoDebito + ' - ' + row.descricaoCentroCustoDebito : null },
    ]);
  };

  const imprimir = () => {
    const values = formProps.getValues();

    const descConta = values.planoContaSaldo ? values.planoContaSaldo.descricao : null;
    const descCentroCusto = values.centroCustoSaldo ? values.centroCustoSaldo.descricao : null;

    movimentacaoRef.current.imprimir(descConta, descCentroCusto);
  };

  const onCloseModal = (update = false) => {
    setModalContent(null);
    if (update) {
      if (modoBusca === 'lancamentos') {
        searchRef.current.notifySearchExpressionChange();
      } else {
        movimentacaoRef.current.doSearch();
      }
    }
  };

  const excluirPeriodo = () =>
    setModalContent(
      <LancamentoExclusaoPeriodoModalView
        onClose={onCloseModal}
        globalParameter={globalReducer.globalParameter}
        expressions={getSearchExpressions()}
      />
    );

  const execAtualizaCalculoIrpjCsll = (referencia, success) => {
    atualizaCalculoIrpjCsll(
      {
        empresaMCG: globalReducer.globalParameter.empresaMCG,
        referencia,
      },
      {
        thenFunction: () => {
          alertManager.emit(ACTION_EVENTS.SHOW, {
            message: 'Atualizao de calculo IRPJ e CSLL efetuado com sucesso',
            onOkClick: () => {
              success();
            },
            type: AlertType.INFORMATION,
          });
        },
      }
    );
  };

  const execRemoverLancamento = (selectedItem, success) => {
    const refExclusao = formatRef(toDate(selectedItem.dtLancamento));
    removerLancamentoLote(
      {
        empresaMCG: globalReducer.globalParameter.empresaMCG,
        listItem: [selectedItem],
        referencia: refExclusao,
        removerLote: false,
      },
      {
        thenFunction: res => {
          if (res) {
            alertManager.emit(ACTION_EVENTS.SHOW, {
              message:
                'O lanamento excludo interfere no clculo de IRPJ ou CSLL j calculado no sistema.  possvel que o ' +
                'procedimento de recalculo possa ser demorado. Deseja recalcular o IRPJ e CSLL desse ano j apurado no sistema ?',
              onNoClick: () => {
                success();
              },
              onOkClick: () => {
                execAtualizaCalculoIrpjCsll(refExclusao, success);
              },
              type: AlertType.QUESTION,
            });
          } else {
            success();
          }
        },
      }
    );
  };

  const removerLancamento = (selectedItem, success, error, cancel) => {
    const bloqueio = validatePeriodoConfig(selectedItem.dtLancamento, globalReducer.globalParameter);
    if (bloqueio) {
      alertManager.emit(ACTION_EVENTS.SHOW, {
        message: 'No  possvel excluir o lanamento ' + bloqueio,
        type: AlertType.ERROR,
      });
      cancel();
    } else {
      const params = {
        data: selectedItem.dtLancamento,
        idGerEmpresa: globalReducer.globalParameter.empresa.id,
      };
      isDataEncerrada(params, {
        thenFunction: isEncerrado => {
          if (isEncerrado) {
            alertManager.emit(ACTION_EVENTS.SHOW, {
              message:
                'Existem contas de resultado encerradas neste perodo. Continuar com essa operao ocasionar a abertura ' +
                'das mesmas. Deseja continuar?',
              onNoClick: () => {
                cancel();
              },
              onOkClick: () => {
                abrirContas(params, {
                  thenFunction: () => {
                    execRemoverLancamento(selectedItem, success);
                  },
                });
              },
              type: AlertType.WARNING_YES_NO,
            });
          } else {
            execRemoverLancamento(selectedItem, success);
          }
        },
      });
    }
  };

  const hiddenCentroCusto = () => {
    let result = true;
    if (globalReducer && globalReducer.globalParameter && globalReducer.globalParameter.lancamentoMCGConfig) {
      result = globalReducer.globalParameter.lancamentoMCGConfig.utilizarCentroCusto === false;
    }
    return result;
  };

  const columns: ColumnsProps[] = [
    {
      dataField: 'dtLancamento',
      sort: true,
      text: 'Data',
    },
    {
      dataField: 'nroLote',
      sort: true,
      text: 'N Lote',
    },
    {
      dataField: 'nroLancamento',
      sort: true,
      text: 'N Lanamento',
    },
    {
      dataField: 'contaDebitoOuCR',
      formatter: contaDebitoOuCRFormatter,
      sort: false,
      text: 'Conta de Dbito',
    },
    {
      dataField: 'descricaoCentroCustoDebito',
      formatter: centroCustoCreditoOuCRFormatter,
      hidden: hiddenCentroCusto(),
      sort: true,
      text: 'Centro de Custo Dbito',
    },
    {
      dataField: 'contaCreditoOuCR',
      formatter: contaCreditoOuCRFormatter,
      sort: false,
      text: 'Conta de Crdito',
    },
    {
      dataField: 'descricaoCentroCustoCredito',
      formatter: centroCustoDebitoOuCRFormatter,
      hidden: hiddenCentroCusto(),
      sort: true,
      text: 'Centro de Custo Crdito',
    },
    {
      dataField: 'valor',
      formatter: decimalFormatter,
      sort: true,
      text: 'Valor',
    },
    {
      dataField: 'historicoCompleto',
      sort: true,
      text: 'Histrico',
    },
    {
      dataField: 'origem',
      formatter: origemFormatter,
      sort: true,
      text: 'Origem',
    },
  ];

  const getExtraSearchButtons = () => (
    <>
      {modoBusca === 'movimentacao' && (
        <Button color="first" onClick={imprimir} className="mr-2">
          <FontAwesomeIcon icon="print" /> Imprimir
        </Button>
      )}
      <Button color="danger" onClick={excluirPeriodo} className="mr-2">
        <FontAwesomeIcon icon="times" /> Excluir por Perodo
      </Button>
    </>
  );

  const getCustomHeader = () => (
    <div className={cx({ 'pb-4': modoBusca === 'movimentacao' })}>
      <div className="d-flex justify-content-between">
        <Button onClick={toggleSearch} color="link" className={cx('pl-0 advanced-search-button', { 'pt-0': modoBusca === 'lancamentos' })}>
          Pesquisa Avanada
          <span className={cx({ 'rotate-arrow': isSearchOpen })}>&nbsp;&raquo;</span>
        </Button>
        {modoBusca === 'movimentacao' && (
          <div>
            {getExtraSearchButtons()}
            <Button color="success" onClick={searchRef.current.openAddView}>
              <FontAwesomeIcon icon="plus" /> Incluir
            </Button>
          </div>
        )}
      </div>
      <Collapse isOpen={isSearchOpen} className={cx({ 'pt-4': modoBusca === 'movimentacao', 'mb-4': modoBusca === 'lancamentos' })}>
        <Card body={true} className="card-search">
          <Row>
            <Col>
              <InputGroupInline
                formProps={formProps}
                isPrependLabel={true}
                id="referencia"
                label="Referncia"
                type={InputType.REFERENCE}
                inputProps={{ onFocus: zerarDatas }}
              />
            </Col>
            <Col md={1} className="text-center" style={{ paddingTop: 10 }}>
              OU
            </Col>
            <Col>
              <InputGroupInline
                formProps={formProps}
                isPrependLabel={true}
                id="dataInicial"
                label="Data Inicial"
                type={InputType.DATE}
                inputProps={{ onFocus: zerarReferencia }}
              />
            </Col>
            <Col>
              <InputGroupInline
                formProps={formProps}
                isPrependLabel={true}
                id="dataFinal"
                label="Data Final"
                type={InputType.DATE}
                inputProps={{ onFocus: zerarReferencia }}
              />
            </Col>
          </Row>
        </Card>
        <Accordion>
          <AccordionItem title="Consulta Lanamento" open={openFromMenu}>
            <Row>
              <Col>
                <PlanoContaIS id="contaDebito" formProps={formProps} label="Conta de Dbito" onChange={zerarContaSaldo} />
              </Col>
            </Row>
            <Row>
              <Col>
                <PlanoContaIS id="contaCredito" formProps={formProps} label="Conta de Crdito" onChange={zerarContaSaldo} />
              </Col>
            </Row>
            <Row>
              <Col>
                <InputGroupInline
                  id="origemLancamento"
                  label="Origem de Lanamento"
                  formProps={formProps}
                  isPrependLabel={true}
                  placeholder="Selecione a Origem do Lanamento"
                  inputDropDownProps={{ optionsProvider: ORIGEM_LANCAMENTO_MCG_ENUM_VALUES }}
                  type={InputType.DROP_DOWN}
                />
              </Col>
            </Row>
          </AccordionItem>
          <AccordionItem title="Consulta Movimentao" open={openFromVerifyError}>
            <div className="d-flex">
              <div className="flex-grow-1">
                <PlanoContaIS
                  id="planoContaSaldo"
                  formProps={formProps}
                  label="Conta"
                  tooltipProps={{
                    message: 'A conta selecionada ser utilizada para o clculo do saldo anterior, dbito, crdito e saldo atual presente no rodap',
                  }}
                  onChange={changeContaSaldo}
                />
              </div>
              <div style={{ width: 400, marginLeft: 20 }}>
                <InputGroupInline
                  id="consolidacaoSaldo"
                  label="Consolidao"
                  formProps={formProps}
                  isPrependLabel={true}
                  inputDropDownProps={{ optionsProvider: CONSOLIDAR_ENUM_VALUES }}
                  type={InputType.DROP_DOWN}
                />
              </div>
            </div>
            <Row>
              <Col>
                <CentroCustoIS id="centroCustoSaldo" formProps={formProps} label="Centro de Custo" onChange={zerarConta} />
              </Col>
            </Row>
          </AccordionItem>
        </Accordion>
        <div className="text-right">
          <Button color="first" onClick={doSearch}>
            <FontAwesomeIcon icon="search" /> Consultar
          </Button>
        </div>
      </Collapse>
    </div>
  );

  const getCustomFooter = () => (
    <Card className="custom-footer mt-4" body={true}>
      <Row>
        <Col>
          <FormGroup>
            <Label>Descrio Conta Dbito:</Label>
            <Input name="descDebito" innerRef={formProps.register} disabled={true} bsSize="sm" />
          </FormGroup>
        </Col>
        <Col>
          <FormGroup>
            <Label>Descrio Conta Crdito:</Label>
            <Input name="descCredito" innerRef={formProps.register} disabled={true} bsSize="sm" />
          </FormGroup>
        </Col>
      </Row>
      <Row>
        <Col>
          <FormGroup>
            <Label>Descrio Centro de Custo Dbito:</Label>
            <Input name="descCustoDebito" innerRef={formProps.register} disabled={true} bsSize="sm" />
          </FormGroup>
        </Col>
        <Col>
          <FormGroup>
            <Label>Descrio Centro de Custo Crdito:</Label>
            <Input name="descCustoCredito" innerRef={formProps.register} disabled={true} bsSize="sm" />
          </FormGroup>
        </Col>
      </Row>
      <Row>
        <Col>
          <FormGroup>
            <Label>Total:</Label>
            <Input name="totalSaldo" innerRef={formProps.register} disabled={true} bsSize="sm" />
          </FormGroup>
        </Col>
        <Col>
          <FormGroup>
            <Label>Total de Dbito:</Label>
            <Input name="totalGridDebito" innerRef={formProps.register} disabled={true} bsSize="sm" />
          </FormGroup>
        </Col>
        <Col>
          <FormGroup>
            <Label>Total de Crdito:</Label>
            <Input name="totalGridCredito" innerRef={formProps.register} disabled={true} bsSize="sm" />
          </FormGroup>
        </Col>
      </Row>
    </Card>
  );

  return (
    <WindowComponent
      title="Lanamento e Consulta de Movimentao"
      useGlobalParameter={true}
      globalParameter={globalReducer.globalParameter}
      dependencies={[
        () => hasEmpresa(globalReducer.globalParameter),
        () => hasEmpresaMCG(globalReducer.globalParameter),
        () => hasGrupoPlanoContas(globalReducer.globalParameter),
      ]}
      wikiId={2302}
    >
      {modalContent}
      <div className={cx('grid-lancamentos', { hidden: modoBusca !== 'lancamentos' })}>
        <SearchDataGrid
          {...props}
          {...props.searchDataGridProps}
          ref={searchRef}
          columns={columns}
          defaultSorted={defaultSorted}
          dtoClassName={CLASS_NAME}
          extraData={getExtraData(props, 'LancamentoSaldoEditView', CLASS_NAME, {
            customRemove: removerLancamento,
            hideCustomSearch: modoBusca === 'movimentacao',
          })}
          searchExpressions={getSearchExpressions}
          customHeaderContent={getCustomHeader()}
          customFooterContent={getCustomFooter()}
          onAfterSearch={afterSearch}
          searchProps={{ extraButtons: getExtraSearchButtons() }}
          selectRow={{ mode: SelectModeEnum.RADIO, onSelect, clickToSelect: true }}
        />
      </div>
      <div className={cx({ 'd-none': modoBusca !== 'movimentacao' })}>
        <LancamentoMovimentacaoDataGrid ref={movimentacaoRef} columns={columns} expressions={getSearchExpressions} />
      </div>
    </WindowComponent>
  );
};

export default LancamentoSaldoSearchView;
